var me; // 表示本人用户，区别于查询的用户
import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"

export default {
	components:{
		uniNavBar
	},
	data() {
		return {
			pageTitle: '关注和粉丝列表',
			scrollLeft: 0,
			isClickChange: false,
			currentTab: '', // 切换 list 0/1
			menuTabs: [{
				name: '他关注的'
			}, {
				name: '关注他的'
			}],

			// 关注粉丝列表属性
			swiperDataList: [
				[], // followList 把数据写进里面首次进入页面加载不出，所以写到外面
				[] // fansList
			],
			followList: '',
			fansList: '',
			myId: '',

			screenWidth: 350,
			serverUrl: "",
			scrollTop: 0,
			old: {
				scrollTop: 0
			}

		}
	},

	changeIndicatorDots(e) {
		this.indicatorDots = !this.indicatorDots
	},
	changeAutoplay(e) {
		this.autoplay = !this.autoplay
	},
	intervalChange(e) {
		this.interval = e.target.value
	},
	durationChange(e) {
		this.duration = e.target.value
	},

	onLoad(opt) {
		var data = JSON.parse(decodeURIComponent(opt.data));
		var thisUserInfo = data.thisUserInfo;
		var currentTab = data.currentTab;

		uni.setNavigationBarTitle({
			title: thisUserInfo.nickname + '的主页'
		});

		me = this.getGlobalUserInfo();
		this.myId = me.id;

		// 获取userId
		var userId = thisUserInfo.id;
		this.queryFansFollow(userId);

		// 设置列表 index
		this.currentTab = currentTab;

		var screenWidth = uni.getSystemInfoSync().screenWidth;
		this.screenWidth = screenWidth;
	},

	onPullDownRefresh() {
		console.log('refresh');
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},

	methods: {
		swichMenu: async function(current) { //点击其中一个 menu
			if (this.currentTab == current) {
				return false;
			} else {
				this.currentTab = current;
				this.setScrollLeft(current);
			}
		},
		swiperChange: async function(e) {
			let index = e.target.current;
			this.setScrollLeft(index);
			this.currentTab = index;
		},
		setScrollLeft: async function(tabIndex) {
			let leftWidthSum = 0;
			for (var i = 0; i <= tabIndex; i++) {
				let nowElement = await this.getWidth('tabNum' + i);
				leftWidthSum = leftWidthSum + nowElement.width;
			}
			let winWidth = uni.getSystemInfoSync().windowWidth;
			this.scrollLeft = leftWidthSum > winWidth ? (leftWidthSum - winWidth) : 0
		},
		getWidth: function(id) { //得到元素的宽高
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select("#" + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					res(data);
				}).exec();
			})
		},
		loadMore: function(tabIndex) {
			console.log('正在加载更多数据。。。')
			this.getDateList(tabIndex);
		},

		upper: function(e) {
			console.log(e)
		},
		lower: function(e) {
			console.log(e)
		},
		scroll: function(e) {
			console.log(e)
			this.old.scrollTop = e.detail.scrollTop
		},
		goTop: function(e) {
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			});
			// uni.showToast({
			// 	icon: "none",
			// 	title: "回到顶部喽~"
			// })
		},

		/**
		 * 添加关注
		 */
		addFollow: function(index1, index2) {
			console.log("加关注...");
			var list;
			if (index1 == 0) {
				list = this.followList;
			} else if (index1 == 1) {
				list = this.fansList;
			}

			var thisUser = list[index2];

			var that = this;
			uni.request({
				url: that.$serverUrl + '/user/follow',
				method: "POST",
				data: {
					userId: thisUser.id,
					fanId: me.id
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					if (res.data.status == 200) {
						// 刷新用户信息，这里本地改就好就不用重新刷新列表了
						thisUser.follow = true;
					}
				}
			});
		},
		/**
		 * 取消关注
		 */
		cancelFollow: function(index1, index2) {
			console.log("取关...");
			var list;
			if (index1 == 0) {
				list = this.followList;
			} else if (index1 == 1) {
				list = this.fansList;
			}

			var thisUser = list[index2];

			var that = this;
			uni.request({
				url: that.$serverUrl + '/user/dontFollow',
				method: "POST",
				data: {
					userId: thisUser.id,
					fanId: me.id
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					if (res.data.status == 200) {
						// 刷新用户信息
						thisUser.follow = false;
					}
				}
			});
		},

		/**
		 * 查询该用户的粉丝和关注用户信息列表
		 */
		queryFansFollow(userId) {
			var that = this;
			uni.request({
				url: that.$serverUrl + '/user/queryFansAndFollow',
				method: "POST",
				data: {
					userId: userId,
					myId: me.id,
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					console.log(res)
					if (res.data.status == 200) {
						var data = res.data.data;
						// console.log(data);
						that.followList = data.followList;
						that.fansList = data.fansList;
					}
				}
			});
		},

		/**
		 * 直接传 item 有 bug, 所以用这个复杂一点的方式曲线救国
		 * @param {Object} index1 0=followList; 1=fansList
		 * @param {Object} index2 列表里的索引
		 */
		goToPersonPublic(index1, index2) {
			var list;
			if (index1 == 0) {
				list = this.followList;
			} else if (index1 == 1) {
				list = this.fansList;
			}

			var userId = list[index2].id
			uni.redirectTo({
				url: '../personpublic/personpublic?userId=' + userId,
			});
		}
	}
}
